export default "<template>\r\n  <nm-box v-bind=\"box\">\r\n    <el-alert title=\"盒子页\" type=\"warning\" description=\"通过设置 page 属性为true，可以让盒子变成一个盒子页，盒子页高度会自动填充满整个父级元素，当内容超出时会有滚动条\" show-icon :closable=\"false\"></el-alert>\r\n    <pre>\r\n    水调歌头·明月几时有\r\n\r\n         苏轼\r\n\r\n    明月几时有？把酒问青天。\r\n\r\n    不知天上宫阙，今夕是何年。\r\n\r\n    我欲乘风归去，又恐琼楼玉宇，高处不胜寒。\r\n\r\n    起舞弄清影，何似在人间？\r\n\r\n    转朱阁，低绮户，照无眠。\r\n\r\n    不应有恨，何事长向别时圆？\r\n\r\n    人有悲欢离合，月有阴晴圆缺，此事古难全。\r\n\r\n    但愿人长久，千里共婵娟\r\n    </pre>\r\n\r\n    <template v-slot:footer>\r\n      <nm-button type=\"success\" text=\"确认\" @click=\"onClick\"/>\r\n    </template>\r\n  </nm-box>\r\n</template>\r\n<script>\r\nexport default {\r\n  data () {\r\n    return {\r\n      box: {\r\n        page: true,\r\n        header: true,\r\n        footer: true,\r\n        title: '盒子页示例',\r\n        icon: 'app',\r\n        fullscreen: true,\r\n        collapse: true\r\n      }\r\n    }\r\n  },\r\n  methods: {\r\n    onClick () {\r\n      this._success('保存成功')\r\n    }\r\n  }\r\n}\r\n</script>\r\n"